import { RefObject } from 'react';

export interface GraphButtonProps {
    onClick: () => void;
    isOpen?: boolean;
    tabIndex?: number;
    accentColor: string;
    label?: string;
}

export const scrollToRef = (ref: RefObject<HTMLElement>) =>
    ref.current &&
    ref.current.scrollIntoView({
        behavior: 'smooth',
        block: 'start',
    });
